<template>
  <div class="beeTable" :style="beeTableStyle">
    <div class="top">
      <div class="topCon">
        <!--  -->
        <searchCondition 
        v-if="hackReset"
        :items="items" 
        :searchBtns="myConfig.searchBtns" 
        :currentPage="myConfig.currentPage"
        :pageSize="myConfig.pageSize"
        :showPage="myConfig.showPage"
        @reset = "resetFun"
        @tableDataOK = "tableDataOK"
        />
      </div>
    </div>
    <div class="bottom">
      <div class="bottomCon">
        <div v-if="tableData.length">
          <div>
            <myTable 
              :tableData = "tableData"
              :currentSearchOptions = "currentSearchOptions"
              :currentUseCode = "currentUseCode"
              :currentUseUrl = "currentUseUrl"

              :currentPage = "myConfig.currentPage"
              :pageSize = "myConfig.pageSize"
              :totalPage = "totalPage"
              :showPage="myConfig.showPage"

              :resultColumnList = "resultColumnList"
            />
          </div>
        </div>
        <div v-else></div>
      </div>
    </div>
  </div>
</template>

<script>
// myConfig 
// {
// 'id': "componentId_00003",
// 'x': 0,
// 'y': 0,
// 'width': 1200,
// 'height': 600,
// 'padding': 0,
// 'exported': true,
// 'showIndexColumn':true,
// 'tableConfig':...,  //
//   ....
// }

import axios from "axios";
import _ from "lodash";
import searchCondition from "./table/searchCondition.vue"
import myTable from "./table/myTable.vue"
import { debug } from 'util';


export default {
  name: "beeTable",
  props: {
    myConfig: null
  },
  data() {
    return {

      resultColumnList:[], //

      //
      //v-if1
      hackReset:true,

      items:[{
        span: 12,
        label: "",
        keyName: '',
        type: 'beeInput',
        placeholder: '',
        value: '',
        rule: {},
      },{
        span: 12,
        label: "",
        keyName: '',
        type: 'beeSelect',
        placeholder: '',
        value: '',
        rule: {},
      },{
        span: 12,
        label: "",
        keyName: '',
        type: 'beeDatePicker',
        placeholder: '',
        value: '',
        rule: {},
      },],
      tableData:[],
      currentSearchOptions:{},
      currentUseCode:{},
      currentUseUrl:{},
      totalPage:0,
    };
  },
  components:{
    searchCondition,
    myTable,
  },
  methods:{
    resetFun(){      
      this.hackReset = false;
      this.$nextTick(() => {
        this.hackReset = true
      })
    },
    tableDataOK(tableData,searchOptions,code,url,totalPage){
      //console.log('==table==')
      // console.log(tableData)
      // console.log(searchOptions)
      // console.log(code)
      // console.log(url)
      this.tableData = tableData;
      this.currentSearchOptions = searchOptions;
      this.currentUseCode = code;
      this.currentUseUrl = url;
      this.totalPage = totalPage;
    },
    //
    async parseConditionArr(arr){

      console.log("===>")
      console.log(arr)
      let newArr = [];
      for(let i=0;i<arr.length;i++){
        let one = arr[i];
        let item = null;
        let isForeign = one.isForeign;
        let dataType = one.dataType;
        if(isForeign){

          let referenceUrl = one.referenceUrl;
          
          let getOptionsData = function() {
              return new Promise((resolve) => {
                axios.get(referenceUrl).then( async (response) => {
                  let d = response.data.data;

                  let options = [];
                  for(let i=0;i<d.length;i++){
                    options.push({
                      value:d[i][one.referenceColumn],
                      label:d[i][one.referenceDisplayColumn]
                    })
                  }

                  resolve(options);
                }).catch((err)=>{
                  alert(""+err)
                })
              })
          }

          let options = await getOptionsData();

          //
          item = {
            label: one.displayName,
            keyName: one.columnName,
            type: 'beeSelect',
            placeholder: one.placeholder,
            defaultValue: one.defaultValue,
            rule: {},
            options: options,
            queryIndex: one.queryIndex,
          }

        }else{
          //
          item = {}
          if(dataType===1){
            //
            item.label = one.displayName;
            item.keyName = one.columnName;
            item.type = "beeInput";
            item.placeholder = one.placeholder;
            item.defaultValue = one.defaultValue;
            item.rule = {};
            item.queryIndex= one.queryIndex;
          }else if(dataType===2){
            //
            item.label = one.displayName;
            item.keyName = one.columnName;
            item.type = "beeInput";
            item.placeholder = one.placeholder;
            item.defaultValue = one.defaultValue;
            item.rule = {};
            item.queryIndex= one.queryIndex;
          }else if(dataType===3){
            //
            item.label = one.displayName;
            item.keyName = one.columnName;
            item.type = "beeDatePicker";
            item.placeholder = one.placeholder;
            item.defaultValue = one.defaultValue;
            item.rule = {};
            item.queryIndex= one.queryIndex;
          }else{
            //
            item = {
              label: "XXXX11",
              keyName: 'XXXX22',
              type: 'beeInput',
              placeholder: 'XXXX33',
              defaultValue: '',
              rule: {},
              queryIndex: one.queryIndex
            }
          }

        }
        newArr.push(item);
      }

      newArr = _.orderBy(newArr,'queryIndex','asc');  

      return newArr;
    }
  },
  async mounted(){
    //console.log('===>')
    //console.log(this.myConfig);

    //
    const conditionArr = this.myConfig.tableConfig.conditionColumnList;
    this.items = await this.parseConditionArr(conditionArr);

    //
    this.resultColumnList = _.orderBy(this.myConfig.tableConfig.resultColumnList,'columnIndex','asc');

  },
  computed: {
    beeTableStyle() {
      let str = "position:absolute;box-sizing:border-box;";
      str += "border:" + this.myConfig.border + ";" 
      str += "width:" + this.myConfig.width + "px;" 
      str += "height:" + this.myConfig.height + "px;" 
      str += "left:" + this.myConfig.x + "px;" 
      str +=  "top:" + this.myConfig.y + "px;"
      str += "color:"+ this.myConfig.color+";"
      str += "font-size:"+this.myConfig['font-size']+"px;"
      str += "text-align:"+this.myConfig['text-align']+";"
      str += "padding:"+this.myConfig['padding']+";"
      str += "background:"+this.myConfig['background']+";"
      return str;
    }
  }
};
</script>

<style lang="scss">
.beeTable {
  color: #000;
  background: #fff;
  box-sizing: border-box;
  .top{
    box-sizing: border-box;
    width:100%;
    background: #fff;
    padding: 20px;
    .topCon{
      box-sizing: border-box;
      width:100%;
      background: #fff;
      padding: 20px;
      border: solid 1px #aaa;
    }
  }
  .bottom{
    box-sizing: border-box;
    width:100%;
    background: #fff;
    margin-top:-20px;
    padding: 20px;
    .bottomCon{
      box-sizing: border-box;
      width:100%;
      background: #fff;
      padding: 20px;
      border: solid 1px #aaa;
    }
  }
}
</style>
