<template>

  <div class="conditionBox">
    <el-row>
      <component
      v-for="(item, index) in items"
      :key="index"
      :is="item.type"
      :item="item"
      :ref="item.keyName"
      @sonChange = "sonChangeHandle"
      ></component>
    </el-row>
    <div class="funBox">
      <el-button @click="resetForm('ruleForm')"></el-button>

      <template v-for="(one,index) in searchBtns">
        <el-button 
        ref = "searchBrn"
        :key= "index" 
        type= "primary" 
        :diyCoreCode= "one.diyCoreCode" 
        @click="submitForm(one.diyCoreCode,one.dataUrl)"
        >
          {{one.text}}
        </el-button>
      </template>
    </div>
  </div>
</template>

<script>
import axios from "axios";

import beeInput from './beeInput.vue';
import beeDatePicker from './beeDatePicker.vue';
import beeSelect from './beeSelect.vue';
import { setTimeout } from 'timers';

export default {
  name: "beeTitle",
  props: {
    items:Array,
    searchBtns:null,
    currentPage:null,
    pageSize:null,
    showPage:null,
  },
  data() {
    return {
      conditionData:{}, //
    };
  },
  methods:{
    //
    initConditionData(arr){
      let newArr={};
      for(let i=0;i<arr.length;i++){
        newArr[arr[i].keyName] = (arr[i].defaultValue);
      }
      console.log('===>')
      console.log(newArr)
      return newArr;
    },
    submitForm(code,url){
      //console.log("")
      //console.log(this.conditionData);
      
      // console.log(code)
      // console.log(url)
      // console.log(this.currentPage)
      // console.log(this.pageSize)

      const body = {
        diyCoreCode:code,
        abc:this.conditionData,
      }

      //
      if(this.showPage){
        body.currentPage=this.currentPage
        body.pageSize=this.pageSize
        //
        axios.post(url,body).then(response => {
          let tableData = response.data.data.recordList;
          let totalPage = response.data.data.recordCount;
          this.$emit('tableDataOK', tableData, this.conditionData,code,url,totalPage); 
        });
      }else{
        //
        axios.post(url,body).then(response => {
          let tableData = response.data.data;
          let totalPage = -1;
          this.$emit('tableDataOK', tableData, this.conditionData,code,url,totalPage); 
        });
      }


    },
    resetForm(){
      this.$emit('reset');
    },
    //
    sonChangeHandle(v,item){
      this.conditionData[item.keyName] = v;
    }
  },
  computed: {
  },
  components:{
    beeInput,
    beeDatePicker,
    beeSelect,
  },
  mounted(){
    //console.log(this.items)
    //console.log(this.searchBtns)
    //console.log(this.$refs['searchBrn'][0])
    console.log()
    setTimeout(()=>{
      this.$refs['searchBrn'][0].$el.click();
    },0)
  },
  updated(){
    //
    this.conditionData = this.initConditionData(this.items);
  }
  
};
</script>

<style lang="scss">
.conditionBox{
  .funBox{
    text-align:right; 
  }
}
</style>
